import React from "react";
import { Form } from "react-bootstrap";

const CoreSelect = ({ name, onChange }) => {
  return (
    <Form.Select name={name} defaultValue={"0"} onChange={onChange}>
      <option value="0">Selecione a quantidade de ncleos de fibra...</option>
      <option value="2">2 ncleos</option>
      <option value="4">4 ncleos</option>
      <option value="8">8 ncleos</option>
      <option value="12">12 ncleos</option>
      <option value="16">16 ncleos</option>
      <option value="24">24 ncleos</option>
    </Form.Select>
  );
};

export default CoreSelect;